*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --accent-color: #4440f6;
  --white-color: #fff;
  --dark-text-color: #111321;
  --light-text-color: #f4f4f4;
  --secondary-text-color: #a8a8a8;
  --light-bg-color: #f4f4f4;
  --dark-bg-color: #2e2e2e;
}

body {
  color: var(--dark-text-color);
  background-color: var(--light-bg-color);
  font-family: Manrope;
  font-size: 32px;
  font-weight: 700;
  line-height: 1.38;
}

button {
  cursor: pointer;
  border: none;
  padding: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  display: block;
}

h1, h2, h3, h4, h5, h6, li, p {
  margin: 0;
  padding: 0;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 320px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 16px;
  padding-right: 16px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}

@font-face {
  font-family: Manrope;
  src: url("Manrope-Bold.33b2b0ee.ttf");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: Manrope;
  src: url("Manrope-Regular.a0d78fb1.ttf");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

.header {
  border-bottom: 1px solid #ececec;
}

.header__container {
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
}

.header__navigation {
  display: flex;
}

.header-logo {
  padding-top: 24px;
  padding-bottom: 24px;
  font-size: 24px;
}

.header-logo:hover, .header-logo:focus {
  color: var(--accent-color);
}

.header-list {
  display: none;
}

.mob-menu {
  background-color: var(--white-color);
  width: 100%;
  height: 100%;
  z-index: 10;
  position: fixed;
  top: 0;
}

.mob-menu .container {
  position: relative;
}

.mob-menu-list {
  margin-top: 68px;
}

.mob-menu__item {
  color: #212121;
  position: relative;
}

.mob-menu__item:not(:last-child) {
