}

.mob-menu__item.active-item {
  background-color: var(--accent-color);
  color: var(--light-bg-color);
}

.mob-menu__link {
  width: 100%;
  letter-spacing: .02em;
  padding-top: 11px;
  padding-bottom: 11px;
  padding-left: 62px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.18;
