}

.open-mob-menu {
  background-color: #0000;
  border: 0;
  justify-content: center;
  align-items: center;
  margin-left: 16px;
  display: flex;
}

.open-search-input, .open-login-form {
  background-color: #0000;
  border: 0;
  align-items: center;
  display: flex;
}

.open-login-form {
  margin-left: 14px;
}

.open-login-form:hover, .open-login-form:focus {
  stroke: var(--accent-color);
}

.close-mob-menu {
  background-color: #0000;
  border: 0;
  display: block;
  position: absolute;
  top: 22px;
  right: 16px;
}

.btn-container {
  padding-top: 32px;
  padding-bottom: 32px;
  display: flex;
}

.input {
  width: 173px;
  height: 31px;
  border-color: #000;
  border-radius: 20px;
