}

.icon-close-mob-menu:hover, .icon-close-mob-menu:focus {
  stroke: var(--accent-color);
}

.icon-open-mob-menu {
  stroke: #000;
}

.icon-open-mob-menu:hover, .icon-open-mob-menu:focus, .icon-open-input:hover, .icon-open-input:focus {
  stroke: var(--accent-color);
}

.mob-menu-top {
  padding-top: 16px;
}

.header-logo-mob {
  padding-top: 0;
  padding-bottom: 0;
  padding-left: 16px;
}

.icon-home {
