  height: 32px;
  border: 1px solid var(--white-color);
  background-color: var(--accent-color);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 50%;
  left: 16px;
  transform: translate(0, -50%);
}

.icon-home-svg {
  stroke: var(--white-color);
  fill: var(--accent-color);
}

.icon-right-arrow {
  stroke: var(--white-color);
