  position: absolute;
  top: 15px;
  right: 15px;
}

.container-login {
  min-height: 100vh;
  text-align: center;
  justify-content: center;
  align-items: center;
  display: flex;
}

.reg {
  color: var(--white-color);
  background-color: var(--accent-color);
}

.log {
