}

.card-news__text {
  width: 353px;
  height: 66px;
  margin-top: 20px;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
  top: 567px;
  left: 1px;
  overflow: hidden;
}

.card-news__box {
  justify-content: space-between;
  margin-top: 20px;
