}

.footer .container {
  height: 150px;
  background-color: #7a7a7a;
}

.filters-date .container {
  justify-content: space-between;
  padding-top: 28px;
  display: flex;
}

.filters-buttons {
  font-size: 12px;
  font-weight: 400;
}

.topmenu {
  gap: 16px;
  display: flex;
}

.topmenu-li {
  position: relative;
}

.topmenu-li button {
  color: var(--accent-color);
  background-color: var(--light-text-color);
  border: 1px solid var(--accent-color);
  border-radius: 20px;
  padding: 7.5px 24px;
  font-size: 12px;
  font-weight: 400;
}

.topmenu-li button:hover {
  background-color: var(--accent-color);
  color: var(--white-color);
}

.submenu {
  background: var(--white-color);
  z-index: 5;
  width: 168px;
  opacity: 0;
  transform-origin: 0 0;
  border-radius: 20px;
  transition: all .5s ease-in-out;
  position: absolute;
  top: 100%;
  left: 0;
  transform: scaleY(0);
}

.submenu button {
  color: var(--accent-color);
  background-color: var(--white-color);
  text-align: left;
  border: none;
  padding: 12px 15px;
  font-family: Manrope;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
}

.submenu button:hover {
  background-color: var(--white-color);
  color: var(--accent-color);
  text-decoration: underline;
}

.submenu li:last-child button {
  border-bottom: none;
}

.topmenu > li:hover .submenu {
  opacity: 1;
  transform: scaleY(1);
}

.cont_disk, .cont_tab {
  display: none;
}

@media screen and (min-width: 768px) {
  .cont_mob, .cont_disk {
    display: none;
  }

  .cont_tab {
