  }
}

.calendar-input.isActive::placeholder {
  color: var(--white-color);
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.calendar-input.isActive {
  background-color: var(--accent-color);
