}

.switch {
  width: 40px;
  height: 20px;
  margin: 0 8px;
  display: inline-block;
  position: relative;
}

.checkbox {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  background-color: #fff;
  border: .604701px solid #4b48db;
  border-radius: 20px;
  transition: all .4s;
  position: absolute;
  inset: 0;
}

.slider:before {
  content: "";
  height: 16px;
  width: 16px;
  background-color: #4b48db;
  border-radius: 50%;
  transition: all .4s;
  position: absolute;
  bottom: 1px;
  left: 1px;
}

.checkbox:checked + .slider {
  background-color: #4b48db;
}

.checkbox:focus + .slider {
  box-shadow: 0 0 1px #4b48db;
}

.checkbox:checked + .slider:before {
  background-color: #fff;
  transform: translateX(20px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.read-date {
  border-bottom: 1px solid #929292;
  position: absolute;
}

.spoiler_wrap {
  height: -moz-fit-content;
  height: fit-content;
  border-radius: 5px;
  margin-bottom: 20px;
  font-size: 22px;
  position: relative;
}

.spoiler_title {
  cursor: pointer;
  letter-spacing: .02em;
  color: #111321;
  font-family: Poppins;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
}

.spoiler_content {
  height: auto;
  max-height: 0;
  opacity: 0;
  color: #444;
  margin-top: -10px;
  padding-bottom: 10px;
  font-size: 14px;
  transition: all .15s ease-out;
  overflow: hidden;
}

.spoiler_content.open {
  max-height: 100%;
  opacity: 1;
  margin-top: 0;
}

.spoiler_title .spoiler_arrow {
  vertical-align: middle;
  margin-bottom: 8px;
  margin-left: 8px;
  margin-right: 8px;
  font-size: 20px;
  transition: all .1s;
  display: inline-block;
}

.spoiler_title .spoiler_arrow svg {
  fill: #111321;
}

.spoiler_title .spoiler_arrow.open {
  margin-bottom: 0;
  transform: rotate(180deg);
}

